@media only screen and (min-width: 1113px) and (max-width: 1114px)
{
	#top-text{
		-webkit-transform: translateX(-27rem);
		        transform: translateX(-27rem);
	}

	img{
		-webkit-transform: translate(-18rem, 5rem);
		        transform: translate(-18rem, 5rem);
	}

	.button{
		-webkit-transform: translate(18rem, -15rem);
		        transform: translate(18rem, -15rem);
	}
}

